import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { RouterModule, Routes } from '@angular/router';
import { DetailSupplierLiabilityComponent } from './detail-supplier-liability.component';
import { NgZorroAntModule } from 'src/app/cafe/desktop/ng-zorro-ant.module';
import { DetailInvoiceComponent } from './detail-invoice/detail-invoice.component';
import { IonicModule } from '@ionic/angular';
import { TranslateModule } from '@ngx-translate/core';
import { DetailReceiptExpensesComponent } from './detail-receipt-expenses/detail-receipt-expenses.component';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { CreateExpensesComponent } from './create-expenses/create-expenses.component';
const routes: Routes = [
  {
    path: '',
    component: DetailSupplierLiabilityComponent
  },
  {
    path: 'detail-invoice',
    component: DetailInvoiceComponent
  },
  {
    path: 'detail-receipt-expenses',
    component: DetailReceiptExpensesComponent
  },
  {
    path: 'create-expenses',
    component: CreateExpensesComponent
  }
]

@NgModule({
  declarations: [
    DetailSupplierLiabilityComponent,
    DetailInvoiceComponent,
    DetailReceiptExpensesComponent,
    CreateExpensesComponent
  ],
  imports: [
    CommonModule,
    RouterModule.forChild(routes),
    NgZorroAntModule,
    IonicModule,
    TranslateModule,
    FormsModule,
    ReactiveFormsModule
  ]
})
export class DetailSupplierLiabilityModule { }
