import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { VhAlgorithm, VhQueryCafe } from 'ionic-vhframeworks';
import { LanguageService } from 'src/app/cafe/services/language.service';

@Component({
  selector: 'app-detail-receipt-expenses',
  templateUrl: './detail-receipt-expenses.component.html',
  styleUrls: ['./detail-receipt-expenses.component.scss']
})
export class DetailReceiptExpensesComponent implements OnInit {

  constructor(
    private router: Router,
    public vhAlgorithm: VhAlgorithm,
    public languageService: LanguageService,
    private vhQueryCafe: VhQueryCafe
  ) {
    let state = this.router.getCurrentNavigation().extras.state
    this.billInfo = { ...state };
    this.data = state.data;
    this.bills = state.bills;
    this.stateObj = state.stateObj;
    this.billInfo['status_bill'] = this.checkBillType(this.billInfo.bill_type);
    if (this.billInfo['id_wallet'] != "id_cash") {
      this.billInfo['payment_type'] = 3;
    }
    else { this.billInfo['payment_type'] = 1 }
  }

  ngOnInit(): void {
    this.list_suppliers = this.vhQueryCafe.getlocalSuppliers();
  }
  stateObj: any;
  keySearch: any;
  selectedValue: any;
  billInfo: any;
  bills: any;
  data: any;
  list_suppliers: any = [];

  checkBillType(bill_type) {
    //kiểm tra loại hóa đơn
    switch (bill_type) {
      case 12: {
        return (this.languageService.translate("Pay for supplier liabilities"));
      }
    }
  }
  goBack() {
    this.router.navigate(['/cafe/dashboard/management/cashflow/supplier-liability/detail-supplier-liability'], {
      state: {
        data: this.data,
        keySearch: this.keySearch,
        selectedValue: this.selectedValue,
        bills: this.bills,
        stateObj: this.stateObj,
      }
    });
  }
}
